@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600;700&display=swap");
html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-padding-top: 0rem;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.4rem;
  padding: 0;
  color: #464646;
}

li {
  list-style: none;
  margin: 0;
  padding: 0;
}

a, .nav-link {
  text-decoration: none;
  color: inherit;
}
a:hover, .nav-link:hover {
  color: #421059;
}

main {
  margin-top: -35px;
}

img, picture {
  max-width: 100%;
  display: block;
}

h2 {
  font-size: 3.6rem;
  font-weight: 700;
  color: #421059;
}

.myBtn {
  height: 5rem;
  padding: 1.2rem 2.8rem;
  font-size: 1.6rem;
  font-weight: 600;
}

.primary {
  color: #421059;
  background-color: #fff;
}
.primary:hover {
  border: 2px solid #fff;
  color: #fff;
  background-color: transparent;
}

.secondary {
  border: 2px solid #fff;
  color: #fff;
  background-color: transparent;
}
.secondary:hover {
  color: #421059;
  background-color: #fff;
}

.complementary {
  color: #105941;
  border: none;
  font-weight: 700;
}

.py-gap {
  padding: 5rem 0;
}

.text {
  font-size: 1.6rem;
  font-weight: 400;
  margin-bottom: 2rem;
  color: #757575;
}

.marked {
  border: 2px solid red;
}

