.blog .latest {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: auto;
}
.blog .latest .blog-card-img {
  width: min(90%, 27rem);
}
.blog .latest .blog-card-img img {
  height: 24rem;
  object-fit: cover;
  border-radius: 8px;
}
.blog .latest .blog-card-body {
  width: min(90%, 27rem);
  height: 14rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}
.blog .latest .blog-card-body h4 {
  color: #105941;
  font-size: 1rem;
}
.blog .latest .blog-card-body h4 i {
  color: #757575;
}
.blog .latest .blog-card-body h3 {
  color: #464646;
  font-size: 1.5rem;
}
.blog .latest .blog-card-body .actions {
  align-self: flex-end;
}

.services {
  height: 105rem;
  background: rgb(33, 1, 59);
  background: linear-gradient(180deg, rgb(33, 1, 59) 0%, rgba(33, 1, 59, 0.9) 33%, rgba(33, 1, 59, 0.55) 67%, rgba(33, 1, 59, 0) 100%);
}
.services .bg-card {
  height: 95rem;
  background-color: #fff;
  box-shadow: 5px 10px 7px 3px rgba(0, 0, 0, 0.25);
  border-radius: 1.6rem;
}
.services .services-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr 1fr;
  gap: 35px;
  grid-auto-flow: row;
}
.services .services-grid .service-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.services .services-grid .service-card .card-img {
  position: relative;
  width: min(90%, 30rem);
}
.services .services-grid .service-card .card-img img {
  height: 240px;
  object-fit: cover;
}
.services .services-grid .service-card .card-body {
  background-color: #fff;
  width: min(90%, 27rem);
  z-index: 20;
  margin-top: -65px;
  box-shadow: 5px 7px 10px 5px rgba(169, 169, 169, 0.3);
}
.services .services-grid .service-card .card-body .subtitle {
  color: rgba(16, 89, 65, 0.4);
  font-size: 1rem;
}
.services .services-grid .service-card .card-body .card-title {
  font-size: 1.6rem;
  color: #421059;
}
.services .services-grid .service-card .card-body .card-text {
  font-size: 1.2rem;
  color: #757575;
}
.services .services-grid .service-card img, .services .services-grid .service-card .card-body {
  border-radius: 0.8rem;
}

.banner {
  height: 40rem;
  background-color: #21013b;
}
.banner h3 {
  color: #fff;
  font-size: 3.2rem;
}

.commitment {
  height: 58rem;
  background-color: #fff;
}
.commitment .com-card {
  background-color: #f4f4f4;
  border-radius: 2rem;
  padding: 2.5rem;
  display: grid;
  grid-template-columns: 1fr 0.1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 4px 4px;
  grid-auto-flow: row;
  grid-template-areas: "img1 img2 img2" "img1 img3 img3";
  box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, 0.25);
  -webkit-box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, 0.25);
}
.commitment .com-card .img1 {
  grid-area: img1;
}
.commitment .com-card .img2 {
  grid-area: img2;
}
.commitment .com-card .img3 {
  grid-area: img3;
}
.commitment .com-content h2 {
  text-align: center;
  font-weight: 700;
  color: #421059;
  margin-bottom: 5rem;
}
.commitment .com-content i {
  color: #421059;
  font-size: 4rem;
}

.features {
  height: 56rem;
  background-color: #21013b;
}
.features .grid {
  width: min(90%, 70rem);
  gap: 5rem;
}
.features .grid .card.custom-card {
  width: 20rem;
  height: 16rem;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #421059;
  padding: 1rem;
}
.features .grid .card.custom-card .card-icon i {
  color: inherit;
  font-size: 3.2rem;
  margin-bottom: 1.5rem;
}
.features .grid .card.custom-card .card-title h2 {
  font-size: 1.6rem;
}

.hero {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.hero .image-bg {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
}
.hero .image-bg .overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 5;
  background: radial-gradient(50% 50% at 50% 50%, rgba(66, 16, 89, 0.9) 0%, rgba(33, 1, 59, 0.9) 100%);
}
.hero .image-bg img, .hero .image-bg picture {
  height: 100vh;
  aspect-ratio: 16;
  min-width: 100%;
}
.hero .hero-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  width: min(90%, 110rem);
  height: 100vh;
  margin: 0 auto;
  position: absolute;
  z-index: 9;
  top: 0;
  left: auto;
  right: auto;
}
.hero .hero-wrapper .hero-image-wrapper {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}
.hero .hero-wrapper .hero-image-wrapper .ring-bg,
.hero .hero-wrapper .hero-image-wrapper .member-bg {
  flex: 1;
}
.hero .hero-wrapper .hero-image-wrapper .ring-bg img,
.hero .hero-wrapper .hero-image-wrapper .member-bg img {
  width: min(100%, 60rem);
}
.hero .hero-content {
  position: absolute;
  z-index: 11;
  color: #fff;
  width: min(90%, 50rem);
  left: 10%;
}
.hero .hero-content .page-title {
  font-size: 4rem;
  font-weight: 700;
  letter-spacing: 0.2rem;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
  color: #fff;
}
.header .top-navbar {
  height: 5rem;
  background-color: rgba(217, 217, 217, 0.4);
}
.header .main-navbar {
  font-weight: 600;
  background-color: rgba(217, 217, 217, 0.2);
}
.header .main-navbar nav {
  height: 7rem;
}
.header .main-navbar nav ul li {
  margin-right: 3.5rem;
  font-size: 1.6rem;
}